@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Serif:wght@200;400&display=swap);
* {
    margin: 0;
    padding: 0;
}

body {
    background-color: #fbfbfb;
    font-family: 'IBM Plex Serif', serif;
    margin: 32px auto;
    display: flex;
    justify-content: center;
}



.footer {
    bottom: 0;
    left: 0;
    padding: 16px;
    position: absolute;
}

.blogWrapper{margin:0 16px}@media screen and (min-width: 900px){.blogWrapper{max-width:640px}}h1{font-size:48px}h2{font-size:36px}h3{font-size:30px}h4{font-size:24px}h5{font-size:20px}h6{font-size:18px}p{font-size:16px}h1,h2,h3,h4,h5,h6,p{color:#222222}h1,h2,h3,h4,h5,h6{margin-top:48px;line-height:1}p{margin:24px 0;line-height:1.7}code{background-color:#f5f5f5;border-radius:4px;font-size:14px;padding:6px}pre{background-color:#e9e9e9;border-radius:4px;padding:12px}pre code{background-color:inherit}ul{margin-left:24px;list-style-position:inside}ul li{font-size:16px}footer{background-color:#141414;color:#fafafa;padding:6px 0;text-align:center;width:100%}

@media screen and (max-width: 400px){.homeWrapper{width:256px}}@media screen and (min-width: 900px){.homeWrapper{min-width:640px}}.homeWrapper footer{bottom:0;background-color:#141414;color:#fafafa;left:0;padding:6px 0;position:fixed;text-align:center;width:100%}h1{margin-top:16px}.homeWrapper a{font-size:18px}

